<template>
  <form action="#" @submit.prevent="getMusic()">
    <input
      @keyup="search()"
      :placeholder="placeholder"
      v-model="query"
      type="text"
      ref="searchInput"
    />
  </form>
</template>

<script>
import axios from "axios";
import { debounce } from "lodash-es";

export default {
  data() {
    return {
      query: "",
      limit: 20,
      placeholder: "Song name, band ...",
    };
  },
  methods: {
    search: debounce(function () {
      this.getMusic();
    }, 500),
    getMusic() {
      axios
        .get(
          `https://itunes.apple.com/search?term=${encodeURI(
            this.query
          )}&entity=musicTrack&limit=${this.limit}`
        )
        .then((response) => {
          let iTunesSongs = response.data.results
            .filter((song) => song.kind === "song")
            .map((song) => this.extractData(song));

          window.eventBus.emit("new-songs-bro", iTunesSongs);
        });
    },
    extractData({
      trackId: id,
      artistName: artist,
      previewUrl: audioFile,
      artworkUrl100: cover,
      trackName: name,
      collectionName: album,
    }) {
      return { id, artist, audioFile, cover, name, album };
    },
  },
  mounted() {
    this.$refs.searchInput.focus();
  },
};
</script>

<style lang="scss" scoped>
input {
  padding: 5px;
  border-radius: 5px;
}
</style>
