<template>
  <transition name="fade" appear>
    <article v-cloak :class="bcgClass">
      <SongPalette @color="handleColor" />
      <div v-if="song.cover">
        <img :src="song.cover" alt="album cover image" />
      </div>
      <p>
        <strong>
          {{ songify(song) }}
        </strong>
      </p>
      <figure v-if="song.audioFile">
        <figcaption>{{ shorten(song.album) }}</figcaption>
        <audio controls :src="song.audioFile">
          Your browser does not support the
          <code>audio</code> element.
        </audio>
      </figure>
    </article>
  </transition>
</template>

<script>
import { truncate } from "lodash-es";
import SongPalette from "@/components/tunes/TunesSongPalette.vue";

export default {
  data() {
    return {
      bcgClass: "",
    };
  },
  props: {
    song: {
      type: Object,
    },
  },
  components: {
    SongPalette,
  },
  methods: {
    handleColor(value) {
      this.bcgClass = value;
    },
    songify(song) {
      return song.artist + " - " + song.name;
    },
    shorten(str, len = 55) {
      return truncate(str, { length: len });
    },
  },
};
</script>

