<template>
  <div>
    <ul class="tunes-list">
      <li>
        <TunesSong
          v-for="song in songs.slice(0, songsShown)"
          :song="song"
          :key="song.id"
        />
      </li>
    </ul>
    <button
      v-if="songs.length > 4 && songsShown < songs.length"
      @click="loadMore"
    >
      Load more songs
    </button>
  </div>
</template>

<script>
import TunesSong from "@/components/tunes/TunesSong.vue";

export default {
  components: {
    TunesSong,
  },
  data() {
    return {
      songs: [],
      songsShown: 5,
    };
  },
  mounted() {
    window.eventBus.on(
      "new-songs-bro",
      (data) => ((this.songs = data), (this.songsShown = 5))
    );
  },
  methods: {
    loadMore() {
      this.songsShown += 5;
    },
  },
};
</script>

